<template>
  <div>
    <div class="level">
      <div class="level-left">
        <a class="button is-link is-expandible" @click="createPatient">
          <span class="icon">
            <i class="fas fa-user-plus"></i>
          </span>
          <span>Register New Patient</span>
        </a>
      </div>
      <div class="level-right">
        <searchbar-component></searchbar-component>
      </div>
    </div>
    <table class="table is-fullwidth">
      <thead>
        <tr>
          <th>First Name</th>
          <th>Last Name</th>
          <th>Email</th>
          <th>Telephone No.</th>
          <th>Sex</th>
          <th>Age</th>
          <th>Actions</th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(patient, key) in patients" :key="key">
          <td class="is-capitalized">{{ patient.first_name }}</td>
          <td class="is-capitalized">{{ patient.last_name }}</td>
          <td>{{ patient.email }}</td>
          <td>{{ patient.telephone }}</td>
          <td>{{ patient.sex }}</td>
          <td>{{ patient.age }}</td>
          <td>
            <button class="button is-link" @click="activateModal(patient)">
              <span>Order Test </span>
            </button>
          </td>
          <td>
            <button
              class="button is-link"
              @click="activateVaccineModal(patient)"
            >
              <span>Order Vaccine </span>
            </button>
          </td>
        </tr>
      </tbody>
    </table>
    <testordering-component></testordering-component>
    <vaccineordering-component></vaccineordering-component>
    <createpatient-component></createpatient-component>
  </div>
</template>

<script>
import PatientSearchComponent from "@/components/PatientSearchComponent";
import CreatePatientComponent from "@/components/CreatePatientComponent";
import TestOrderingComponent from "@/components/TestOrderingComponent";
import VaccineOrderingComponent from "@/components/VaccineOrderingComponent";

export default {
  name: "PatientRegistrationComponent",
  components: {
    "searchbar-component": PatientSearchComponent,
    "createpatient-component": CreatePatientComponent,
    "testordering-component": TestOrderingComponent,
    "vaccineordering-component": VaccineOrderingComponent
  },
  created() {
    let token = this.$store.state.authentication.inMemoryToken;
    this.$store.dispatch("fetchPatients", token);
    let store = this.$store;
    this.$socket.on("create-patient", function(data) {
      store.dispatch("addNewPatient", data);
    });
  },
  methods: {
    createPatient() {
      this.$store.dispatch("toggleCreatePatientModalOn", true);
    },
    updateIsActive(value) {},
    activateModal(patient) {
      this.$store.dispatch("toggleCreateTestModalOn", true);
      this.$store.dispatch("updateActivePatient", patient);
    },
    activateVaccineModal(patient) {
      this.$store.dispatch("toggleCreateVaccineModalOn", true);
      this.$store.dispatch("updateVaccineActivePatient", patient);
    }
  },
  computed: {
    patients() {
      return this.$store.state.receptionist.patients;
    }
  }
};
</script>
