<template>
  <div class="modal" :class="{ 'is-active': active }">
    <div class="modal-background"></div>
    <div class="modal-card">
      <header class="modal-card-head">
        <p class="modal-card-title">Register New Patient</p>
        <button class="delete" aria-label="close" @click="closeModal"></button>
      </header>
      <div>
        <form @submit.prevent="submitForm" ref="newPatientForm">
          <section class="modal-card-body">
            <div class="field is-grouped is-grouped-multiline">
              <div class="control is-expanded">
                <input
                  v-model="firstName"
                  class="input"
                  type="text"
                  placeholder="First Name"
                  required
                />
              </div>
              <div class="control is-expanded">
                <input
                  v-model="lastName"
                  class="input"
                  type="text"
                  placeholder="Last Name"
                  required
                />
              </div>
            </div>
            <div class="field">
              <div class="control has-icons-left">
                <input
                  v-model="email"
                  class="input"
                  type="email"
                  placeholder="Email"
                  required
                />
                <span class="icon is-small is-left">
                  <i class="fas fa-envelope"></i>
                </span>
              </div>
            </div>
            <div class="field is-expanded">
              <div class="field has-addons">
                <p class="control">
                  <a class="button is-static">
                    +237
                  </a>
                </p>
                <p class="control is-expanded">
                  <input
                    v-model="telephone"
                    class="input"
                    type="tel"
                    placeholder="Phone Number"
                    required
                  />
                </p>
              </div>
            </div>
            <div class="field is-grouped is-grouped-multiline">
              <div class="control">
                <div class="select">
                  <select v-model="sex" required>
                    <option selected disabled value="">Sex</option>
                    <option>Male</option>
                    <option>Female</option>
                  </select>
                </div>
              </div>
              <div class="control is-expanded">
                <input
                  v-model.number="age"
                  class="input"
                  type="number"
                  placeholder="Age"
                  number
                  required
                />
              </div>
            </div>
          </section>
          <footer class="modal-card-foot">
            <input
              type="submit"
              value="Save Patient"
              class="button is-link is-fullwidth is-medium"
              :disabled="!isFormValid"
            />
          </footer>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import { generateID } from "../helpers/utility";

export default {
  name: "CreatePatientComponent",
  data() {
    return {
      firstName: null,
      lastName: null,
      email: null,
      telephone: null,
      sex: "",
      age: null
    };
  },
  methods: {
    closeModal() {
      this.$store.dispatch("toggleCreatePatientModalOn", false);
    },
    validateForm() {
      return (
        this.firstName &&
        this.lastName &&
        this.email &&
        this.telephone &&
        this.sex &&
        this.age
      );
    },
    refreshForm() {
      this.firstName = null;
      this.lastName = null;
      this.email = null;
      this.telephone = null;
      this.sex = "";
      this.age = null;
    },
    submitForm() {
      if (this.validateForm) {
        let patient = {
          first_name: this.firstName,
          last_name: this.lastName,
          email: this.email,
          telephone: this.telephone,
          age: this.age,
          sex: this.sex,
          type: "patient",
          key: generateID(),
          created_at: Date.now(),
          updated_at: Date.now()
        };
        this.$socket.emit("create-patient", patient);
        this.closeModal();
        this.refreshForm();
      }
    }
  },
  computed: {
    active() {
      return this.$store.state.receptionist.isCreatePatientModalOn;
    },
    isFormValid() {
      return this.validateForm();
    }
  }
};
</script>
