<template>
  <div class="modal" :class="{'is-active': active}">
    <div class="modal-background"></div>
    <div class="modal-card">
      <header class="modal-card-head">
        <p
          class="modal-card-title"
          v-if="patient"
        >Patient: {{ patient.first_name + ' ' + patient.last_name}}</p>
        <button class="delete" aria-label="close" @click="closeModal"></button>
      </header>
      <section class="modal-card-body">
        <div>
          <div class="card">
            <header class="card-header">
              <p class="card-header-title">Doctor's Details:</p>
            </header>
            <div class="card-content">
              <div class="content">
                <div class="field">
                  <div class="control">
                    <input
                      class="input"
                      type="text"
                      v-model="doctorFullName"
                      placeholder="Full Name"
                    />
                  </div>
                </div>
                <div class="field">
                  <div class="control">
                    <input class="input" type="email" v-model="doctorEmail" placeholder="Email" />
                  </div>
                </div>
                <div class="field is-expanded">
                  <div class="field has-addons">
                    <p class="control">
                      <a class="button is-static">+237</a>
                    </p>
                    <p class="control is-expanded">
                      <input
                        v-model="doctorTelephone"
                        class="input"
                        type="tel"
                        placeholder="Phone Number"
                        required
                      />
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="card" style="margin-top: 1em;">
            <header class="card-header">
              <p class="card-header-title">Vaccines:</p>
            </header>
            <div style="margin: 0.5em; padding-bottom: 0.5em; border-bottom: 2px solid #dbdbdb;">
              <v-select
                @input="addVaccine"
                v-model="selected"
                placeholder="Add vaccine+"
                :options="vaccineOptions"
              ></v-select>
            </div>
            <div class="card-content">
              <div v-show="isVaccineOrdered">
                <table class="table is-fullwidth">
                  <thead>
                    <tr>
                      <th></th>
                      <th>Test Name</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="(vaccine, key) in vaccinesOrdered" :key="key">
                      <button
                        class="button is-danger is-outlined"
                        @click="removeVaccine(vaccine.name)"
                      >
                        <span class="icon is-small">
                          <i class="far fa-trash-alt"></i>
                        </span>
                      </button>
                      <td>{{ vaccine.name}}</td>
                    </tr>
                  </tbody>
                </table>
              </div>
              <div v-show="!isVaccineOrdered">
                <strong>No Vaccine Has Been Selected</strong>
              </div>
            </div>
          </div>
          <div style="margin-top: 15px;">
            <button
              class="button is-link is-fullwidth is-large"
              @click="submitOrder"
              :disabled="isFormReady"
            >Submit</button>
          </div>
        </div>
      </section>
    </div>
  </div>
</template>
<script>
import { generateID } from "../helpers/utility";

export default {
  name: "VaccineOrderingComponent",
  created() {
    let token = this.$store.state.authentication.inMemoryToken;
    this.$store.dispatch("fetchVaccines", token);
  },
  data() {
    return {
      doctorFullName: null,
      doctorEmail: null,
      doctorTelephone: null,
      vaccinesOrdered: [],
      selected: ""
    };
  },
  methods: {
    closeModal() {
      this.$store.dispatch("toggleCreateVaccineModalOn", false);
      this.$store.dispatch("updateVaccineActivePatient", null);
    },
    validateOrder() {
      return (
        this.doctorFullName &&
        this.doctorEmail &&
        this.doctorTelephone &&
        this.isVaccineOrdered
      );
    },
    refreshForm() {
      this.doctorFullName = null;
      this.doctorEmail = null;
      this.doctorTelephone = null;
      this.vaccinesOrdered = [];
      this.selected = "";
    },
    submitOrder() {
      let labRequest = {
        vaccines: this.vaccinesOrdered,
        patient: this.$store.state.receptionist.vaccineActivePatient,
        doctor: {
          name: this.doctorFullName,
          email: this.doctorEmail,
          telephone: this.doctorTelephone
        },
        users: [this.$store.state.authentication.currentUser],
        status: "created",
        created_at: Date.now(),
        updated_at: Date.now(),
        order_time: Date.now(),
        total_cost: this.calculateTotalCost(this.orderedExams),
        key: generateID(),
        type: "labrequest"
      };
      if (this.validateOrder()) {
        this.$socket.emit("create-labrequest", labRequest);
        this.closeModal();
        this.refreshForm();
      }
    },
    addVaccine(vaccine) {
      if (vaccine) {
        this.vaccinesOrdered.push(vaccine.vaccine);
      }
    },
    removeVaccine(name) {
      let index = -1;
      for (let i = 0; i < this.vaccinesOrdered.length; i++) {
        if (this.vaccinesOrdered[i].name === name) {
          index = i;
        }
      }
      if (index != -1) {
        this.vaccinesOrdered.splice(index, 1);
      }
    },
    calculateTotalCost() {
      if (this.vaccinesOrdered.length === 0) {
        return 0;
      } else {
        let reducer = (accumulator, vaccine) => {
          return vaccine.price + accumulator;
        };
        return this.vaccinesOrdered.reduce(reducer, 0);
      }
    }
  },
  computed: {
    active() {
      return this.$store.state.receptionist.isCreateVaccineModalOn;
    },
    isVaccineOrdered: function() {
      return this.vaccinesOrdered.length >= 1;
    },
    isFormReady: function() {
      return !this.validateOrder();
    },
    vaccineOptions() {
      return this.$store.getters.vaccinesToBeOrdered;
    },
    patient() {
      return this.$store.state.receptionist.vaccineActivePatient;
    }
  }
};
</script>
<style scoped>
@import "../../node_modules/vue-select/dist/vue-select.css";
</style>