<template>
  <div class="modal" :class="{ 'is-active': active }">
    <div class="modal-background"></div>
    <div class="modal-card">
      <header class="modal-card-head">
        <p class="modal-card-title" v-if="patient">
          Patient: {{ patient.first_name + " " + patient.last_name }}
        </p>
        <button class="delete" aria-label="close" @click="closeModal"></button>
      </header>
      <section class="modal-card-body">
        <div>
          <div class="card">
            <header class="card-header">
              <p class="card-header-title">Doctor's Details:</p>
            </header>
            <div class="card-content">
              <div class="content">
                <div class="field">
                  <div class="control">
                    <input
                      class="input"
                      type="text"
                      v-model="doctorFullName"
                      placeholder="Full Name"
                    />
                  </div>
                </div>
                <div class="field">
                  <div class="control">
                    <input
                      class="input"
                      type="email"
                      v-model="doctorEmail"
                      placeholder="Email"
                    />
                  </div>
                </div>
                <div class="field is-expanded">
                  <div class="field has-addons">
                    <p class="control">
                      <a class="button is-static">+237</a>
                    </p>
                    <p class="control is-expanded">
                      <input
                        v-model="doctorTelephone"
                        class="input"
                        type="tel"
                        placeholder="Phone Number"
                        required
                      />
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="card" style="margin-top:1em;">
            <header class="card-header">
              <p class="card-header-title">Exams:</p>
            </header>
            <div
              style="margin: 0.5em; padding-bottom: 0.5em; border-bottom: 2px solid #dbdbdb;"
            >
              <v-select
                @input="selectExam"
                v-model="selected"
                placeholder="Add exam+"
                :options="examOptions"
              ></v-select>
            </div>
            <div class="card-content">
              <div v-show="isTestOrdered">
                <table class="table is-fullwidth">
                  <thead>
                    <tr>
                      <th></th>
                      <th>Test Name</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="(exam, key) in orderedExams" :key="key">
                      <td>
                        <button
                          class="button is-danger is-outlined"
                          @click="removeExam(exam.name)"
                        >
                          <span class="icon is-small">
                            <i class="far fa-trash-alt"></i>
                          </span>
                        </button>
                      </td>
                      <td>{{ exam.name }}</td>
                    </tr>
                  </tbody>
                </table>
              </div>
              <div v-show="!isTestOrdered">
                <strong>No Exam Has Been Selected</strong>
              </div>
            </div>
          </div>
          <div style="margin-top: 15px;">
            <button
              class="button is-link is-fullwidth is-large"
              @click="submitOrder"
              :disabled="isFormReady"
            >
              Submit
            </button>
          </div>
        </div>
      </section>
    </div>
  </div>
</template>
<script>
import { generateID, calculateTotalCost } from "../helpers/utility";

export default {
  name: "TestOrderingComponent",
  data() {
    return {
      doctorFullName: null,
      doctorEmail: null,
      doctorTelephone: null,
      exams: [],
      orderedExams: [],
      selected: ""
    };
  },
  created() {
    this.$store.dispatch(
      "fetchExams",
      this.$store.state.authentication.inMemoryToken
    );
  },
  methods: {
    closeModal() {
      this.$store.dispatch("toggleCreateTestModalOn", false);
      this.$store.dispatch("updateActivePatient", null);
    },
    validateOrder() {
      return (
        this.doctorFullName &&
        this.doctorEmail &&
        this.doctorTelephone &&
        this.isTestOrdered
      );
    },
    refreshForm() {
      this.doctorFullName = null;
      this.doctorEmail = null;
      this.doctorTelephone = null;
      this.exams = [];
      this.orderedExams = [];
      this.selected = "";
    },
    submitOrder() {
      let labRequest = {
        exams: this.orderedExams,
        patient: this.$store.state.receptionist.activePatient,
        doctor: {
          name: this.doctorFullName,
          email: this.doctorEmail,
          telephone: this.doctorTelephone
        },
        users: [this.$store.state.authentication.currentUser],
        status: "created",
        created_at: Date.now(),
        updated_at: Date.now(),
        order_time: Date.now(),
        key: generateID(),
        type: "labrequest"
      };
      labRequest.total_cost = calculateTotalCost(labRequest);
      if (this.validateOrder()) {
        this.$socket.emit("create-labrequest", labRequest);
        this.closeModal();
        this.refreshForm();
      }
    },
    selectExam(exam) {
      if (exam) {
        this.orderedExams.push(exam.exam);
      }
    },
    removeExam(name) {
      let index = -1;
      for (let i = 0; i < this.orderedExams.length; i++) {
        if (this.orderedExams[i].name === name) {
          index = i;
        }
      }
      if (index != -1) {
        this.orderedExams.splice(index, 1);
      }
    }
  },
  computed: {
    active() {
      return this.$store.state.receptionist.isCreateTestModalOn;
    },
    isTestOrdered: function() {
      return this.orderedExams.length >= 1;
    },
    isFormReady: function() {
      return !this.validateOrder();
    },
    examOptions() {
      return this.$store.getters.examsToBeOrdered;
    },
    patient() {
      return this.$store.state.receptionist.activePatient;
    }
  }
};
</script>
<style scoped>
@import "../../node_modules/vue-select/dist/vue-select.css";
</style>
